<script>
import "../CSS/animate.css";
import "../CSS/blog.css";

export default {
  data() {
    return {
      scroH: 0,
      music:
        "https://music.163.com/outchain/player?type=2&id=1367158075&auto=1&height=66",
      styleMusic: {
        backgroundPositionY: -32 + "px",
      },
      imgAnimation: {
        showLeft: {
          showLeft: true,
        },
        showLeftSlow: {
          showLeftSlow: true,
        },
        fadeIn: {
          fadeIn: true,
        },
        fadeInSlow: {
          fadeInSlow: true,
        },
        showRight: {
          showRight: true,
        },
        showBottom: {
          showBottom: true,
        },
        showBottomBlur: {
          showBottomBlur: true,
        },
        showTop: {
          showTop: true,
        },
      },
    };
  },
  methods: {
    //监听滚动方法
    handleScroll() {
      // console.log(document.documentElement.scrollTop, '距离顶部的距离')
      this.scroH = document.documentElement.scrollTop;
    },
    closeMusic() {
      if (this.music != "") {
        this.music = "";
        this.styleMusic = { backgroundPositionY: 0 + "px" };
      } else {
        this.music =
          "https://music.163.com/outchain/player?type=2&id=1367158075&auto=1&height=66";
        this.styleMusic = { backgroundPositionY: -32 + "px" };
      }
    },
  },
  computed: {
    sakuraFall() {
      return this.scroH / 20 - 200;
    },
  },
  mounted() {
    window.addEventListener("scroll", this.handleScroll, true);
  },
};
</script>

<style>
